/* src/pages/c-cont-02/c-cont-02-write.css */
.content-write {
    background: white;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    padding: 24px;
    max-width: 900px;
    margin: 0 auto;
  }
  
  /* 헤더 */
  .write-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    padding-bottom: 20px;
    border-bottom: 1px solid #e9ecef;
  }
  
  .write-header h2 {
    font-size: 24px;
    font-weight: 600;
    color: #212529;
    margin: 0;
  }
  
  .header-buttons {
    display: flex;
    gap: 12px;
  }
  
  .list-button,
  .save-button {
    padding: 10px 20px;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .list-button {
    background: white;
    border: 1px solid #dee2e6;
    color: #495057;
  }
  
  .list-button:hover {
    border-color: #adb5bd;
    background-color: #f8f9fa;
  }
  
  .save-button {
    background-color: #4852EA;
    border: none;
    color: white;
  }
  
  .save-button:hover {
    background-color: #3943d0;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
  }
  
  /* 폼 */
  .write-form {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  
  .form-group label {
    font-size: 14px;
    font-weight: 600;
    color: #495057;
  }
  
  /* 라디오 그룹 */
  .radio-group {
    display: flex;
    gap: 24px;
  }
  
  .radio-label {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    font-size: 15px;
    color: #212529;
  }
  
  .radio-label input[type="radio"] {
    width: 18px;
    height: 18px;
    cursor: pointer;
  }
  
  /* 입력 필드 */
  .form-group input[type="text"],
  .form-group textarea {
    padding: 12px 16px;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 15px;
    color: #212529;
    transition: all 0.2s;
    font-family: inherit;
  }
  
  .form-group input[type="text"]:focus,
  .form-group textarea:focus {
    outline: none;
    border-color: #4852EA;
    box-shadow: 0 0 0 3px rgba(72, 82, 234, 0.1);
  }
  
  .form-group textarea {
    resize: vertical;
    min-height: 300px;
  }
  
  /* 이미지 업로드 */
  .image-upload {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  
  .upload-button {
    display: inline-flex;
    align-items: center;
    padding: 10px 16px;
    background-color: #f8f9fa;
    border: 1px dashed #dee2e6;
    border-radius: 8px;
    font-size: 14px;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .upload-button:hover {
    border-color: #4852EA;
    color: #4852EA;
    background-color: #f0f2ff;
  }
  
  .uploaded-image {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 10px 16px;
    background-color: #f8f9fa;
    border: 1px solid #dee2e6;
    border-radius: 8px;
  }
  
  .image-name {
    font-size: 14px;
    color: #495057;
  }
  
  .remove-button {
    background: none;
    border: none;
    color: #dc3545;
    font-size: 18px;
    cursor: pointer;
    padding: 0 4px;
  }
  
  .remove-button:hover {
    color: #c82333;
  }
  
  /* 이미지 미리보기 */
  .image-preview {
    margin-top: 16px;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    overflow: hidden;
  }
  
  .image-preview img {
    width: 100%;
    max-width: 400px;
    height: auto;
    display: block;
  }
  
  /* 반응형 */
  @media (max-width: 768px) {
    .write-header {
      flex-direction: column;
      align-items: flex-start;
      gap: 16px;
    }
  
    .header-buttons {
      width: 100%;
      justify-content: flex-end;
    }
  
    .form-group textarea {
      min-height: 200px;
    }
  }