/* src/pages/c-pay-01/c-pay-01.css */
.settlement-container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* 헤더 */
  .settlement-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }
  
  .settlement-header h2 {
    font-size: 24px;
    font-weight: 600;
    color: #212529;
    margin: 0;
  }
  
  .excel-download-btn {
    padding: 10px 20px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .excel-download-btn:hover {
    background-color: #218838;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(40, 167, 69, 0.3);
  }
  
  /* 날짜 선택 */
  .date-selector {
    margin-bottom: 24px;
  }
  
  .date-button {
    padding: 10px 20px;
    background-color: white;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 500;
    color: #212529;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .date-button:hover {
    border-color: #4852EA;
    color: #4852EA;
  }
  
  /* 정산 요약 카드 */
  .settlement-summary {
    background: white;
    border-radius: 12px;
    padding: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    margin-bottom: 24px;
  }
  
  .settlement-summary h3 {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    margin: 0 0 20px 0;
  }
  
  .summary-card {
    background-color: #f8f9fa;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    padding: 24px;
  }
  
  .summary-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    font-size: 16px;
  }
  
  .summary-item.total {
    justify-content: center;
    font-weight: 600;
    color: #495057;
    margin-bottom: 20px;
  }
  
  .summary-item .label {
    color: #495057;
  }
  
  .summary-item .amount {
    font-weight: 600;
  }
  
  .summary-item .amount.positive {
    color: #28a745;
  }
  
  .summary-item .amount.negative {
    color: #dc3545;
  }
  
  .summary-divider {
    border-top: 2px solid #dee2e6;
    margin: 20px 0;
  }
  
  .summary-total {
    text-align: center;
  }
  
  .summary-total .label {
    display: block;
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    margin-bottom: 8px;
  }
  
  .summary-total .desc {
    display: block;
    font-size: 14px;
    color: #6c757d;
  }
  
  /* 상세 거래 내역 */
  .transaction-details {
    background: white;
    border-radius: 12px;
    padding: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  }
  
  .transaction-details h3 {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    margin: 0 0 20px 0;
  }
  
  .transaction-table {
    overflow-x: auto;
  }
  
  .transaction-table table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .transaction-table th {
    text-align: left;
    padding: 12px;
    font-size: 14px;
    font-weight: 600;
    color: #495057;
    background-color: #f8f9fa;
    border-bottom: 2px solid #dee2e6;
  }
  
  .transaction-table td {
    padding: 16px 12px;
    border-bottom: 1px solid #e9ecef;
    font-size: 14px;
    color: #212529;
  }
  
  .transaction-table .net-amount {
    font-weight: 600;
    color: #28a745;
  }
  
  .transaction-table .fee-amount {
    color: #dc3545;
  }
  
  /* 날짜 선택 모달 */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .date-modal {
    background: white;
    border-radius: 12px;
    width: 400px;
    max-width: 90%;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    border-bottom: 1px solid #e9ecef;
  }
  
  .modal-header h3 {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    margin: 0;
  }
  
  .close-button {
    background: none;
    border: none;
    font-size: 24px;
    color: #6c757d;
    cursor: pointer;
    padding: 0;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .close-button:hover {
    color: #212529;
  }
  
  .modal-body {
    padding: 24px;
  }
  
  .year-selector {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 24px;
    margin-bottom: 24px;
  }
  
  .year-selector button {
    background: none;
    border: none;
    font-size: 20px;
    color: #4852EA;
    cursor: pointer;
    padding: 4px 8px;
  }
  
  .year-selector span {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
  }
  
  .month-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 8px;
  }
  
  .month-button {
    padding: 12px;
    background-color: white;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 14px;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .month-button:hover {
    border-color: #4852EA;
    color: #4852EA;
  }
  
  .month-button.selected {
    background-color: #4852EA;
    border-color: #4852EA;
    color: white;
  }
  
  .modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    padding: 20px 24px;
    border-top: 1px solid #e9ecef;
  }
  
  .cancel-button,
  .search-button {
    padding: 10px 20px;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .cancel-button {
    background: white;
    border: 1px solid #dee2e6;
    color: #495057;
  }
  
  .cancel-button:hover {
    background-color: #f8f9fa;
  }
  
  .search-button {
    background-color: #4852EA;
    border: none;
    color: white;
  }
  
  .search-button:hover {
    background-color: #3943d0;
  }
  
  /* 반응형 */
  @media (max-width: 768px) {
    .settlement-header {
      flex-direction: column;
      align-items: flex-start;
      gap: 16px;
    }
  
    .transaction-table {
      font-size: 12px;
    }
  
    .transaction-table th,
    .transaction-table td {
      padding: 8px;
    }
  
    .month-grid {
      grid-template-columns: repeat(3, 1fr);
    }

    /* 테이블 스타일 개선 */
    .transaction-table th:nth-child(1) { width: 8%; }  /* 일자 */
    .transaction-table th:nth-child(2) { width: 15%; } /* 고객/차량 */
    .transaction-table th:nth-child(3) { width: 25%; } /* 서비스 */
    .transaction-table th:nth-child(4) { width: 12%; } /* 결제방식 */
    .transaction-table th:nth-child(5) { width: 13%; } /* 총액 */
    .transaction-table th:nth-child(6) { width: 13%; } /* 수수료 */
    .transaction-table th:nth-child(7) { width: 14%; } /* 정산액 */

    /* 결제방식 스타일 */
    .payment-type {
    display: inline-block;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 13px;
    font-weight: 500;
    }

    .payment-type.mobile {
    background-color: #e3f2fd;
    color: #1976d2;
    }

    .payment-type.onsite {
    background-color: #f3e5f5;
    color: #7b1fa2;
    }

    /* 금액 스타일 */
    .fee-negative {
    color: #dc3545;
    }

    .net-positive {
    color: #28a745;
    font-weight: 600;
    }

    .net-negative {
    color: #dc3545;
    font-weight: 600;
    }

    /* 반응형 테이블 */
    @media (max-width: 1024px) {
    .transaction-table {
        font-size: 13px;
    }
    
    .transaction-table th,
    .transaction-table td {
        padding: 12px 8px;
    }
    }

    @media (max-width: 768px) {
    .settlement-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 16px;
    }
    
    .excel-download-btn {
        width: 100%;
    }
    
    .transaction-table {
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }
    
    .transaction-table table {
        min-width: 600px;
    }
    }
  }