/* src/components/DateModal/DateModal.css */
.date-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .date-modal {
    background: white;
    border-radius: 12px;
    width: 400px;
    max-width: 90%;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
    animation: modalSlideIn 0.3s ease-out;
  }
  
  @keyframes modalSlideIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .date-modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    border-bottom: 1px solid #e9ecef;
  }
  
  .date-modal-header h3 {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    margin: 0;
  }
  
  .date-modal-close {
    background: none;
    border: none;
    font-size: 24px;
    color: #6c757d;
    cursor: pointer;
    padding: 0;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    transition: all 0.2s;
  }
  
  .date-modal-close:hover {
    color: #212529;
    background-color: #f8f9fa;
  }
  
  .date-modal-body {
    padding: 24px;
  }
  
  .year-selector {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 24px;
    margin-bottom: 24px;
  }
  
  .year-selector button {
    background: none;
    border: none;
    font-size: 20px;
    color: #4852EA;
    cursor: pointer;
    padding: 8px 12px;
    border-radius: 4px;
    transition: all 0.2s;
  }
  
  .year-selector button:hover {
    background-color: #f0f2ff;
  }
  
  .year-selector span {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    min-width: 80px;
    text-align: center;
  }
  
  .month-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 8px;
  }
  
  .month-button {
    padding: 12px;
    background-color: white;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 14px;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .month-button:hover {
    border-color: #4852EA;
    color: #4852EA;
    background-color: #f0f2ff;
  }
  
  .month-button.selected {
    background-color: #4852EA;
    border-color: #4852EA;
    color: white;
  }
  
  .date-modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    padding: 20px 24px;
    border-top: 1px solid #e9ecef;
  }
  
  .date-modal-cancel,
  .date-modal-confirm {
    padding: 10px 20px;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .date-modal-cancel {
    background: white;
    border: 1px solid #dee2e6;
    color: #495057;
  }
  
  .date-modal-cancel:hover {
    background-color: #f8f9fa;
    border-color: #adb5bd;
  }
  
  .date-modal-confirm {
    background-color: #4852EA;
    border: none;
    color: white;
  }
  
  .date-modal-confirm:hover {
    background-color: #3943d0;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
  }
  
  /* 반응형 */
  @media (max-width: 480px) {
    .date-modal {
      width: 100%;
      margin: 16px;
    }
    
    .month-grid {
      grid-template-columns: repeat(3, 1fr);
    }
    
    .date-modal-body {
      padding: 20px;
    }
  }