.performance-container {
    max-width: 1000px;
    margin: 0 auto;
  }
  
  /* 헤더 */
  .performance-header {
    margin-bottom: 24px;
  }
  
  .performance-header h2 {
    font-size: 24px;
    font-weight: 600;
    color: #212529;
    margin: 0;
  }
  
  /* 기간 선택 */
  .date-range-selector {
    background: white;
    border-radius: 12px;
    padding: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    margin-bottom: 24px;
  }
  
  .date-inputs {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 12px;
  }
  
  .date-inputs span {
    font-size: 15px;
    color: #495057;
  }
  
  .date-input {
    padding: 10px 16px;
    background-color: white;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 15px;
    color: #212529;
    cursor: pointer;
    transition: all 0.2s;
    min-width: 140px;
  }
  
  .date-input:hover {
    border-color: #4852EA;
    color: #4852EA;
  }
  
  .search-btn {
    padding: 10px 24px;
    background-color: #4852EA;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .search-btn:hover {
    background-color: #3943d0;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
  }
  
  .date-info {
    font-size: 13px;
    color: #6c757d;
    margin: 0;
  }
  
  /* 현황 테이블 */
  .performance-table {
    background: white;
    border-radius: 12px;
    padding: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  }
  
  .performance-table table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .performance-table th {
    text-align: left;
    padding: 12px;
    font-size: 14px;
    font-weight: 600;
    color: #495057;
    background-color: #f8f9fa;
    border-bottom: 2px solid #dee2e6;
  }
  
  .performance-table td {
    padding: 16px 12px;
    border-bottom: 1px solid #e9ecef;
    font-size: 15px;
    color: #212529;
  }
  
  .value-cell {
    font-weight: 600;
    color: #4852EA;
  }
  
  .detail-btn {
    padding: 6px 16px;
    background-color: white;
    border: 1px solid #dee2e6;
    border-radius: 6px;
    font-size: 13px;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .detail-btn:hover {
    border-color: #4852EA;
    color: #4852EA;
    background-color: #f0f2ff;
  }
  
  /* 빈 데이터 메시지 */
  .empty-message {
    text-align: center;
    padding: 40px !important;
    color: #6c757d;
    font-style: italic;
  }
  
  /* 반응형 - 통합된 하나의 미디어 쿼리 */
  @media (max-width: 768px) {
    .performance-header h2 {
      font-size: 20px;
    }
    
    .date-range-selector {
      padding: 20px;
    }
    
    .date-inputs {
      flex-direction: column;
      align-items: stretch;
      gap: 10px;
    }
    
    .date-inputs > span:nth-child(3) {
      text-align: center;
    }
    
    .date-input {
      width: 100%;
      min-width: auto;
    }
    
    .search-btn {
      width: 100%;
      margin-top: 10px;
    }
    
    .performance-table {
      padding: 16px;
    }
    
    .performance-table th,
    .performance-table td {
      padding: 12px 8px;
      font-size: 14px;
    }
    
    .detail-btn {
      padding: 4px 12px;
      font-size: 12px;
    }
  }