/* src/pages/c-cont-02/c-cont-02.css */
.content-management {
    background: white;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    padding: 24px;
  }
  
  /* 헤더 */
  .content-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }
  
  .content-header h2 {
    font-size: 24px;
    font-weight: 600;
    color: #212529;
    margin: 0;
  }
  
  .new-post-button {
    padding: 10px 20px;
    background-color: #4852EA;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .new-post-button:hover {
    background-color: #3943d0;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
  }
  
  /* 탭 필터 */
  .content-tabs {
    display: flex;
    gap: 8px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #e9ecef;
  }
  
  .tab {
    padding: 8px 16px;
    background: none;
    border: 1px solid #dee2e6;
    border-radius: 20px;
    font-size: 14px;
    color: #6c757d;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .tab:hover {
    border-color: #4852EA;
    color: #4852EA;
  }
  
  .tab.active {
    background-color: #4852EA;
    border-color: #4852EA;
    color: white;
  }
  
  /* 테이블 */
  .content-table {
    margin-bottom: 24px;
  }
  
  .content-table table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .content-table th {
    text-align: left;
    padding: 12px;
    font-size: 14px;
    font-weight: 600;
    color: #495057;
    border-bottom: 2px solid #dee2e6;
  }
  
  .content-table td {
    padding: 16px 12px;
    border-bottom: 1px solid #e9ecef;
  }
  
  /* 종류 뱃지 */
  .type-badge {
    display: inline-block;
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 13px;
    font-weight: 500;
  }
  
  .type-badge.notice {
    background-color: #fff3cd;
    color: #856404;
  }
  
  .type-badge.posting {
    background-color: #d1ecf1;
    color: #0c5460;
  }
  
  /* 제목 셀 */
  .title-cell {
    font-weight: 500;
    color: #212529;
  }
  
  /* 액션 버튼 */
  .action-cell {
    text-align: right;
  }
  
  .edit-button,
  .delete-button {
    padding: 6px 12px;
    margin-left: 8px;
    border: 1px solid #dee2e6;
    border-radius: 6px;
    background: white;
    font-size: 13px;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .edit-button:hover {
    border-color: #4852EA;
    color: #4852EA;
  }
  
  .delete-button:hover {
    border-color: #dc3545;
    color: #dc3545;
  }
  
  /* 페이지네이션 */
  .pagination {
    display: flex;
    justify-content: center;
    gap: 4px;
  }
  
  .page-button {
    padding: 8px 12px;
    border: 1px solid #dee2e6;
    background: white;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .page-button:hover {
    border-color: #4852EA;
    color: #4852EA;
  }
  
  .page-button.active {
    background-color: #4852EA;
    border-color: #4852EA;
    color: white;
  }