/* src/pages/c-cs-02/c-cs-02.css */
.cs-container {
    max-width: 800px;
    margin: 0 auto;
    background: white;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    overflow: hidden;
  }
  
  /* 헤더 */
  .cs-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    border-bottom: 1px solid #e9ecef;
    background-color: #f8f9fa;
  }
  
  .cs-header h2 {
    font-size: 20px;
    font-weight: 600;
    color: #212529;
    margin: 0;
  }
  
  .back-button {
    background: none;
    border: none;
    font-size: 16px;
    color: #6c757d;
    cursor: pointer;
    padding: 8px;
    transition: color 0.2s;
  }
  
  .back-button:hover {
    color: #4852EA;
  }
  
  /* 폼 */
  .cs-form {
    padding: 32px 24px;
  }
  
  .form-section {
    margin-bottom: 32px;
  }
  
  .form-section h3 {
    font-size: 16px;
    font-weight: 600;
    color: #495057;
    margin-bottom: 20px;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    display: block;
    font-size: 14px;
    font-weight: 500;
    color: #495057;
    margin-bottom: 8px;
  }
  
  .required {
    color: #dc3545;
    font-weight: 700;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 12px 16px;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 15px;
    color: #212529;
    transition: all 0.2s;
    font-family: inherit;
  }
  
  .form-group input::placeholder,
  .form-group textarea::placeholder {
    color: #adb5bd;
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    outline: none;
    border-color: #4852EA;
    box-shadow: 0 0 0 3px rgba(72, 82, 234, 0.1);
  }
  
  .form-group textarea {
    resize: vertical;
    min-height: 120px;
  }
  
  .char-count {
    text-align: right;
    font-size: 13px;
    color: #6c757d;
    margin-top: 4px;
  }
  
  /* 제출 버튼 */
  .submit-button {
    width: 100%;
    padding: 16px;
    background-color: #4852EA;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
  }
  
  .submit-button:hover {
    background-color: #3943d0;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
  }
  
  .submit-button:active {
    transform: translateY(0);
  }
  
  /* 반응형 */
  @media (max-width: 768px) {
    .cs-container {
      border-radius: 0;
      box-shadow: none;
    }
    
    .cs-header {
      padding: 16px;
    }
    
    .cs-form {
      padding: 20px 16px;
    }
    
    .form-section {
      margin-bottom: 24px;
    }
  }